import Card from "./Card";
import { motion, AnimatePresence } from "framer-motion";
import { Tabs, TabsContent, TabsTrigger, TabsList } from '../../components/ui/tabs';

///////icon/////////
import { FaHtml5, FaCss3Alt, FaJs, FaReact, FaGraduationCap } from "react-icons/fa";

import React from "react";

const skillsData = [
    {
        section: "Frontend",
        skills: ["HTML", "CSS", "Responsive Web Design", "Tailwind", "Bootstrap", "JavaScript (ES6+)", "jQuery", "React.js", "Next.js"],
    },
    {
        section: "Backend",
        skills: ["Node.js", "Express.js", "TypeScript", "EJS", "Authentication & Authorization (JWT)", "Databases (MongoDB, MySQL)", "RESTful APIs", "Postman", "PHP & WordPress (Beginner)"],
    },
    {
        section: "Version Control",
        skills: ["Git", "GitHub", "Bitbucket"],
    },
    {
        section: "DevOps & Cloud",
        skills: ["AWS EC2", "Docker", "CI/CD", "Cloudflare DNS"],
    },
    {
        section: "Other Skills",
        skills: ["iOS (Swift, UIKit)", "Leadership", "Problem-Solving", "Communication", "Collaboration", "Adaptability", "Time Management"],
    },
];

const education = [
    {
        type: 'education',
        name: 'Bachelor of Computer Applications (BCA)',
        icon: <FaGraduationCap />,
        duration: '2020-2023',
        description: 'Completed Bachelor of Computer Applications (BCA) from IGNOU, Delhi, graduating with First Division. During the program, I gained comprehensive knowledge and hands-on experience in software development, web technologies, and database management. The curriculum equipped me with the skills to design, develop, and maintain efficient digital solutions while strengthening problem-solving and analytical abilities.'
    },
];

const Cards = () => {
    return (
        <>
            <Tabs defaultValue="skill" className="w-full flex flex-col items-center">
                <TabsList className='max-w-max mb-[30px]'>
                    <TabsTrigger value='skill'>My Skills</TabsTrigger>
                    <TabsTrigger value='experience'>Experience</TabsTrigger>
                    <TabsTrigger value='education'>Education</TabsTrigger>
                </TabsList>
                <TabsContent value='skill' className='w-full'>
                    <div className="bg-white rounded-xl shadow-lg p-6 flex-1 transition-transform transform hover:-translate-y-3 hover:shadow-2xl">
                        <h3 className="text-2xl font-bold text-gray-800 mb-4 border-b pb-2">{section}</h3>
                        <ul className="grid grid-cols-2 gap-2">
                            {skills.map((skill, index) => (
                                <li
                                    key={index}
                                    className="bg-gray-100 text-gray-800 font-medium px-4 py-2 rounded-md hover:bg-gray-200 transition-colors"
                                >
                                    {skill}
                                </li>
                            ))}
                        </ul>
                    </div>
                </TabsContent>
                <TabsContent value='education' className='w-full'>
                    <div className="w-full max-w-4xl mx-auto p-6 bg-white shadow-lg rounded-lg my-6">
                        {education.map((edu, index) => (
                            <div key={index} className="flex flex-col md:flex-row items-start md:items-center gap-6">
                                <div className="flex-shrink-0 text-blue-500">
                                    {edu.icon}
                                </div>
                                <div>
                                    <h2 className="text-2xl font-semibold">{edu.name}</h2>
                                    <p className="text-gray-500 italic mb-2">{edu.duration}</p>
                                    <p className="text-gray-700">{edu.description}</p>
                                </div>
                            </div>
                        ))}
                    </div>
                </TabsContent>
            </Tabs>
        </>
    );
};

const Cards = () => {
    return (
        <div className="bg-gray-50 py-10">
            <div className="max-w-6xl mx-auto px-4">
                <Tabs defaultValue="skill" className="w-full flex flex-col items-center">
                    <TabsList className='max-w-max mb-10'>
                        <TabsTrigger value='skill'>My Skills</TabsTrigger>
                        <TabsTrigger value='experience'>Experience</TabsTrigger>
                        <TabsTrigger value='education'>Education</TabsTrigger>
                    </TabsList>
                    <TabsContent value='skill' className='w-full'>
                        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                            {skillsData.map((skillItem, index) => (
                                <SkillCard
                                    key={index}
                                    section={skillItem.section}
                                    skills={skillItem.skills}
                                />
                            ))}
                        </div>
                    </TabsContent>
                </Tabs>
            </div>
        </div>
    );
};


export default Cards;
